"use client";
import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { useSearchParams, useRouter } from "next/navigation";
import { getImageUrl, Movie } from "../../lib/getImageUrl";

const TMDB_API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY || "1070730380f5fee0d87cf0382670b255";
const TMDB_BASE = "https://api.themoviedb.org/3";

export default function MoviesPage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const page = parseInt(searchParams.get("page") || "1", 10);
  const [movies, setMovies] = useState<Movie[]>([]);
  const [totalPages, setTotalPages] = useState(1);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setLoading(true);
    fetch(`${TMDB_BASE}/movie/popular?api_key=${TMDB_API_KEY}&page=${page}`)
      .then(res => res.json())
      .then(data => {
        setMovies(data.results || []);
        setTotalPages(Math.min(data.total_pages, 500)); // TMDB max 500
        setLoading(false);
      });
  }, [page]);

  const goToPage = (p: number) => {
    router.push(`/movies?page=${p}`);
  };

  return (
    <div className="max-w-7xl mx-auto px-4 py-8">
      <h1 className="text-3xl font-extrabold text-white mb-8 drop-shadow-lg">Popular Movies</h1>
      {loading ? (
        <div className="text-white text-lg">Loading...</div>
      ) : (
        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-6">
          {movies.map((movie) => (
            <Link key={movie.id} href={`/watch/movie/${movie.id}`} className="group">
              <div className="glass-card rounded-2xl overflow-hidden shadow-xl transition-all duration-200">
                <div className="relative w-full aspect-[2/3] bg-black/30">
                  <Image
                    src={getImageUrl(movie)}
                    alt={movie.title || ''}
                    fill
                    sizes="200px"
                    className="object-cover w-full h-full group-hover:scale-105 transition-transform duration-200"
                  />
                </div>
                <div className="p-3">
                  <div className="font-bold text-base text-white truncate" title={movie.title}>{movie.title}</div>
                  <div className="text-white/80 text-xs mt-1 line-clamp-2">{movie.overview}</div>
                </div>
              </div>
            </Link>
          ))}
        </div>
      )}
      {/* Pagination */}
      <div className="flex justify-center items-center gap-4 mt-10">
        <button
          className="glass-card px-4 py-2 rounded-lg text-white font-semibold disabled:opacity-40"
          onClick={() => goToPage(page - 1)}
          disabled={page <= 1}
        >
          Previous
        </button>
        <span className="text-white font-bold">Page {page} of {totalPages}</span>
        <button
          className="glass-card px-4 py-2 rounded-lg text-white font-semibold disabled:opacity-40"
          onClick={() => goToPage(page + 1)}
          disabled={page >= totalPages}
        >
          Next
        </button>
      </div>
    </div>
  );
} 